.mapboxgl-compare {
  background-color:#fff;
  position:absolute;
  width:2px;
  height:100%;
  z-index:1;
}
.mapboxgl-compare .compare-swiper-vertical {
  background-color:#3887be;
  box-shadow:inset 0 0 0 2px #fff;
  display:inline-block;
  border-radius:50%;
  position:absolute;
  width:60px;
  height:60px;
  top:50%;
  left:-30px;
  margin:-30px 1px 0;
  color: #fff;
  cursor:ew-resize;
	background-image:url(data:image/svg+xml;base64,);
  }

.mapboxgl-compare-horizontal {
  position:relative;
  width:100%;
  height:2px;
}
.mapboxgl-compare .compare-swiper-horizontal {
  background-color:#3887be;
  box-shadow:inset 0 0 0 2px #fff;
  display:inline-block;
  border-radius:50%;
  position:absolute;
  width:60px;
  height:60px;
  top:50%;
  left:50%;
  margin:-30px 1px 0;
  color: #fff;
  cursor:ns-resize;
  background-image:url(data:image/svg+xml;base64,);
  transform: rotate(90deg)
}
